import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { fetchImages } from '../../actions/searchAction';
import { fetchAmount } from '../../actions/searchAction';

import SelectField from 'material-ui/SelectField';
import MenuItem from 'material-ui/MenuItem';

const customStyle = {
  width: 200,
  margin: '0px 5px',
};

class ImgQty extends Component {
  render() {
    return (
      <SelectField
        name='amount'
        className='SelectField'
        value={this.props.amount}
        onChange={(e, index, value) => { this.props.fetchAmount(value); this.props.fetchImages() }}
        floatingLabelText='Quantity'
        style={customStyle}
      >
        <MenuItem value={9} primaryText='9' />
        <MenuItem value={15} primaryText='15' />
        <MenuItem value={30} primaryText='30' />
        <MenuItem value={50} primaryText='50' />
        <MenuItem value={100} primaryText='100' />
        {/* <MenuItem value={} primaryText='50' /> */}
      </SelectField>
    )
  }
}

ImgQty.propTypes = {
  fetchImages: PropTypes.func.isRequired,
  fetchAmount: PropTypes.func.isRequired,
  amount: PropTypes.number.isRequired,
}

const mapStateToProps = state => ({
  amount: state.search.amount,
})

export default connect(mapStateToProps, { fetchImages, fetchAmount })(ImgQty)


