import React, { Component } from 'react';

import ImgOrientation from './SearchFilters/ImgOrientation';
import ImgCategory from './SearchFilters/ImgCategory';
import ImgColor from './SearchFilters/ImgColor';
import ImgQty from './SearchFilters/ImgQty';
import ImgType from './SearchFilters/ImgType';
import ImgOrder from './SearchFilters/ImgOrder';
// import ColorChip from './SearchFilters/Chip/ColorChip';

import { Toolbar, ToolbarGroup, ToolbarSeparator, ToolbarTitle } from 'material-ui/Toolbar';
import { orange100, blueGrey50 } from 'material-ui/styles/colors';

class FiltersBar extends Component {
  render() {
    const styles = {
      rootComponent: {
        // position: 'relative',
        // top: 57,
      },
      filtersBar: {
        backgroundColor: blueGrey50,
        width: '100%',
        justifyContent: 'space-around',
        // justifyItems: 'center',
      },
    }
    return (
      <div style={styles.rootComponent}>
        <Toolbar style={styles.filtersBar}>
          <ToolbarGroup >
            {/* <ImgQty /> */}
            <ImgOrder />
            <ImgOrientation />
            <ImgCategory />
            <ImgColor />
            <ImgType />
            {/* <ColorChip 
              value={this.props.color}
              onChange={(value) => { this.props.fetchAmount(value); this.props.fetchImages()}}
            /> */}
          </ToolbarGroup>
        </Toolbar>
      </div>
    )
  }
}

export default FiltersBar


